<script>
/*<div class="search-results">
        <div class="search-result">
          <div
            style="
              background-color: rgb(189, 189, 189);
              width: 40px;
              height: 40px;
            "
            class="search-image"
          ></div>
          <div class="search-content">
            <h4>Product Title</h4>
            <p>Price</p>
          </div>
        </div>
      </div>*/

import axios from "axios";

export default {
  data() {
    return {
      products: null,
      search_input: null,
      token: "",
      username: "",
      local_storage: null,
      apiURL: "https://tabletopid-cms.herokuapp.com",
      logging: false,
      registering: false,
      navbarMenuActive: false,
      navbarSearchActive: false,
      isDesktop: window.matchMedia("(min-width: 992px)").matches,
      pages: [
        { title: "Beranda", key: "home", link: "index" },
        { title: "Katalog", key: "catalogue", link: "catalogue" },
        { title: "Kontak", key: "contact", link: "contact" },
        { title: "Tentang", key: "about", link: "about" },
      ],
    };
  },
  mounted() {
    window.addEventListener("resize", this.windowResize);
  },
  unmounted() {
    window.removeEventListener("resize", this.windowResize);
  },
  methods: {
    windowResize() {
      this.isDesktop = window.matchMedia("(min-width: 992px)").matches;
      this.$refs.navbar.classList.add("resize-animation-stopper");
      let resizeTimer = setTimeout(() => {
        this.$refs.navbar.classList.remove("resize-animation-stopper");
      }, 400);
    },
    toggleNavbar() {
      this.navbarMenuActive = !this.navbarMenuActive;
    },
    openLogin() {
      this.logging = true;
    },
    openRegister() {
      this.registering = true;
    },
    closeLogin() {
      this.logging = false;
      this.registering = false;
    },
    register(event) {
      try {
        let elements = event.target.elements;
        const request = {
          username: elements.username.value,
          email: elements.email.value,
          password: elements.password.value,
        };
        axios
          .post(`${this.apiURL}/auth/local/register`, request)
          .then((res) => {
            if (res.status == 200) {
              localStorage.setItem("token", res.data.jwt);
              localStorage.setItem("username", res.data.user.username);
              this.local_storage = localStorage;
              this.$emit("login");
            }
          });
        elements.username.value = "";
        elements.email.value = "";
        elements.password.value = "";
        this.closeLogin();
      } catch (error) {}
    },
    login(event) {
      try {
        let elements = event.target.elements;
        const request = {
          identifier: elements.identifier.value,
          password: elements.password.value,
        };
        axios.post(`${this.apiURL}/auth/local`, request).then((res) => {
          if (res.status == 200) {
            localStorage.setItem("token", res.data.jwt);
            localStorage.setItem("username", res.data.user.username);
            this.local_storage = localStorage;
            this.$emit("login");
          }
        });
        elements.identifier.value = "";
        elements.password.value = "";
        this.closeLogin();
      } catch (error) {}
    },
    logout() {
      this.local_storage = null;
      localStorage.clear();
      this.$emit("logout");
    },
    openSearch() {
      this.navbarSearchActive = true;
    },
    closeSearch() {
      this.navbarSearchActive = false;
    },
  },
  watch: {
    local_storage(newLocalStorage) {
      const token = localStorage.getItem("token");
      this.token = token;

      const username = localStorage.getItem("username");
      this.username = username;
    },
    search_input(newInput) {
      if (newInput === "") {
        this.products = null;
        return;
      }
      axios
        .get(`${this.apiURL}/products?title_contains=${newInput}&_limit=6`)
        .then((response) => {
          this.products = response.data;
        })
        .catch((error) => (this.products = {}));
    },
  },
};
</script>

