<template>
  <header>
    <NavigationBar />
    <div class="hero placeholder" ref="hero">
      <div class="flex-col">
        <h2>Dari pemain, untuk pemain.</h2>
        <p>
          TableTopID menghadirkan permainan tabletop dengan harga lokal dan
          kualitas original.
        </p>
        <a href="catalogue"><button>Belanja</button></a>
      </div>
    </div>
  </header>
  <main :class="{ windows: isWindows }">
    <div class="featured-items flex-col">
      <h3>Produk Unggulan</h3>
      <div
        v-for="(product, index) in products"
        :key="index"
        class="featured-item"
        :id="'item' + (index + 1)"
      >
        <img
          :src="product.image[0].url"
          :alt="product.image[0].alternativeText"
          class="featured-image image"
        />
        <div class="featured-text">
          <h4>{{ product.title }}</h4>
          <p>
            {{
              product.description.length <= 24
                ? product.description
                : product.description.substring(0, 240) + "..."
            }}
          </p>
          <a :href="'/products?id=' + product.id" class="buy-btn">Buy</a>
        </div>
      </div>
    </div>
    <div class="about">
      <div ref="aboutImage" class="about-image placeholder image"></div>
      <div class="about-text">
        <h3>Mengapa kami?</h3>
        <p>
          Barang di TableTopID dijamin originalitasnya dengan harga yang
          terjangkau. Kami sudah bertahun-tahun melayani pemain tabletop
          Indonesia. Lorem ipsum, dolor sit amet consectetur adipisicing elit.
          Deleniti nihil animi repudiandae? Sunt ut ullam quod labore earum
          voluptate, libero optio possimus at reiciendis numquam voluptates,
          sint illo dolores praesentium cum inventore!
        </p>
      </div>
    </div>
    <div class="partners flex-col">
      <h3>Partner</h3>
      <div class="partners-images">
        <img
          v-for="partner in partners"
          :key="partner.id"
          :src="partner.logo[0].url"
          :alt="partner.name"
        />
      </div>
      <a href="https://www.freepik.com/vectors/background" class="credits"
        >Partners' vector logo created by rawpixel.com - www.freepik.com</a
      >
    </div>
  </main>
  <footer>
    <Footer />
  </footer>
</template>

<script>
import axios from "axios";

import NavigationBar from "../../components/NavigationBar.vue";
import Footer from "../../components/Footer.vue";

export default {
  name: "App",
  components: {
    NavigationBar,
    Footer,
  },
  data() {
    return {
      featuredText: [],
      products: null,
      apiURL: "https://tabletopid-cms.herokuapp.com",
      assets: null,
      partners: null,
    };
  },
  computed: {
    isWindows() {
      if (navigator.userAgent.indexOf("Win") != -1) {
        return true;
      }
    },
  },
  mounted() {
    axios
      .get(`${this.apiURL}/products?_limit=3&_sort=sales:DESC`)
      .then((response) => {
        this.products = response.data;
      })
      .catch((error) => (this.products = {}));
    axios.get(`${this.apiURL}/assets`).then((response) => {
      this.assets = response.data;
    });
    axios
      .get(`${this.apiURL}/partners`)
      .then((response) => (this.partners = response.data));
  },
  watch: {
    assets(newAssets) {
      this.$refs.hero.style.backgroundImage = `url(${
        newAssets.find((el) => el["key"] === "hero").medias[0].url
      })`;
      this.$refs.aboutImage.style.backgroundImage = `url(${
        newAssets.find((el) => el["key"] === "logo").medias[0].url
      })`;
    },
  },
};
</script>

